.search-form {
  text-align: center;
  margin-top: 20px;
}

input,
button {
  width: 300px;
  font-size: 16px;
  padding: 6px 0 4px 10px;
  border: 1px solid #cecece;
  background: #f6f6f6;
  border-radius: 8px;
}

.gallery {
  display: flex;
  flex-wrap: wrap;
}

li {
  list-style-type: none;
}

p {
  margin: 0;
}

img {
  width: 300px;
  height: 200px;
}

.stats {
  display: flex;
}

.photo-card {
  margin: 15px;
}

.stats-item {
  margin-right: 10px;
}

i {
  vertical-align: bottom;
}

.load-more {
  display: none;
  margin-left: auto;
  margin-right: auto;
}

.non-hidden {
  display: block;
}
